import React from 'react';
import './DraftRoundItem.scss'

export class DraftRoundItem extends React.Component {

    handleError = (e) => {
        e.target.src = '/projects/fourbifour/images/greyImage.png'
    }

    render() {
        let {
            itemId,
            title,
            data,
            draftType
        } = this.props;

        return <div className="draftrounditem">
            {/*<label htmlFor="{itemId}">{title}</label>*/}
            <img src={'/projects/fourbifour/images/' + title?.split(" ").join("") + this.props.draftType + '.png'} onError={this.handleError} height="50" width="50"/>
            <input type="text" id={itemId} name={itemId} value={data} onChange={this.props.onTextChange}/>
        </div>;
    }    
}