import React from 'react';
import './DraftGrid.scss'
import { DraftRound } from '../DraftRound/DraftRound';

export class DraftGrid extends React.Component {
    render() {
        let {
            order,
            data
        } = this.props

        if (!order) {
            order = [undefined,undefined,undefined,undefined]
        }
        
        let reversed = Array.from(order).reverse()

        return <div className="draftgrid">
            <DraftRound order={order} roundNumber={1} data={data ? data[0] : undefined} onTextChange={this.props.onTextChange} draftType={this.props.draftType}/>
            <DraftRound order={reversed} roundNumber={2} data={data ? data[1] : undefined} onTextChange={this.props.onTextChange} draftType={this.props.draftType}/>
            <DraftRound order={order} roundNumber={3} data={data ? data[2] : undefined} onTextChange={this.props.onTextChange} draftType={this.props.draftType}/>
            <DraftRound order={reversed} roundNumber={4} data={data ? data[3] : undefined} onTextChange={this.props.onTextChange} draftType={this.props.draftType}/>
        </div>;
    }    
}