import React from 'react';
import './DraftOrderSelection.scss'
import { DraftOrderSelectionOption } from '../DraftOrderSelectionOption/DraftOrderSelectionOption';

export class DraftOrderSelection extends React.Component {

    // updateOrder = (e) => {
    //     this.props.onOrderChange(parseInt(e.target.id),e.target.options[e.target.selectedIndex].value);
    // }

    render() {
        let {
            options,
            data
        } = this.props;

        return <div className="draftorderselection">
            {
                options && options.map((v,i) => {
                    return <DraftOrderSelectionOption selectId={i} onOrderChange={this.props.onOrderChange} options={options} key={i} data={data ? data[i] : undefined}/>
                })
            }
        </div>;
    }    
}