import React from 'react';
import './Draft.scss'
import { DraftOrderSelection } from '../DraftOrderSelection/DraftOrderSelection/DraftOrderSelection';
import { DraftGrid } from '../DraftGrid/DraftGrid';
import { DraftReview } from '../DraftReview/DraftReview';

export class Draft extends React.Component {

    render() {
        let {
            title,
            people,
            data,
            imgPath
        } = this.props;

        return <div className="draft elementcontainer">
            <div>
                <h2>{title ? title : "Draft"}</h2>
                <img src={imgPath} height="500"></img>
            </div>
            <h3>Draft Order</h3>
            <DraftOrderSelection options={people} onOrderChange={this.props.onOrderChange} data={data?.order}/>
            <DraftGrid order={data?.order} data={data?.data} onTextChange={this.props.onTextChange} draftType={this.props.draftType}/>
            <DraftReview options={people} data={data?.ranking} name={title} onRankingChange={this.props.onRankingChange}/>
        </div>;
    }    
}