import React from 'react';
import './DraftOrderSelectionOption.scss'

export class DraftOrderSelectionOption extends React.Component {

    updateOrder = (e) => {
        this.props.onOrderChange(parseInt(e.target.id),e.target.options[e.target.selectedIndex].value);
    }

    render() {
        let {
            selectId,
            onOrderChange,
            options,
            data
        } = this.props;

        let shouldBeDefault = !! (data != "Cam" && data != "Fred" && data != "PA" && data != "Mitch White")

        return <div className="draftorderselectionoption">
            {selectId !== undefined && <p>
                {selectId+1 + ":"}
            </p>} 
            <select id={selectId} onChange={this.props.onOrderChange ? this.updateOrder : undefined} className="normal-select-1">
                {shouldBeDefault && <option key={-1} selected>-</option>}
                {!shouldBeDefault && <option key={-1}>-</option>}
                {
                    options && options.map((value,index) => {
                        var selected = !!(value == data)
                        if (selected)
                            return <option key={index} value={value} className="select-dropdown__list-item" selected>{value}</option>
                        else
                            return <option key={index} value={value} className="select-dropdown__list-item" >{value}</option>
                    }, this)
                }
            </select>
        </div>;
    }    
}