import React from 'react';
import './Calendar.scss'

import ReactCalendar from 'react-calendar'

export class Calendar extends React.Component {

    selectFilledInDays = ({ date, view }) => {
        if (view === "month") {
            if (this.props.podcasts && this.props.podcasts.some((p) => new Date(Date.parse(p.date)).toDateString() === date.toDateString())) {
                return 'podcastDay'
            }
        }
    }

    dateChange = (d) => {
        this.props.dateChange(d);
    }

    render() {
        return <div className="calendar">
            <h2>Select a date to start</h2>
            <ReactCalendar showNeighboringMonth={false} tileClassName={this.selectFilledInDays} onChange={this.dateChange}/>
        </div>;
    }    
}