import React from 'react';
import './PodcastForm.scss'

import { Draft } from '../../common/Draft/Draft/Draft'
import { savePodcastEndpoint, apiUrl } from '../../config'

export class PodcastForm extends React.Component {

    handleSubmit = (event) => {
        event.preventDefault()
        var rushmore = event.target.childNodes[0]
        var flushmore = event.target.childNodes[1]

        var rushmoreSelection = rushmore.childNodes[2]
        var rushmoreDataGrid = rushmore.childNodes[3]
        var rushmoreResult = Array.from(rushmore.childNodes[4].childNodes[1].childNodes).find(n => n.checked)?.id
        if (!rushmoreResult) {
            rushmoreResult = ""
        }

        var flushmoreSelection = flushmore.childNodes[2]
        var flushmoreDataGrid = flushmore.childNodes[3]
        var flushmoreResult = Array.from(flushmore.childNodes[4].childNodes[1].childNodes).find(n => n.checked)?.id
        if (!flushmoreResult) {
            flushmoreResult = ""
        }

        var rushmoreSelections= []
        rushmoreSelection.childNodes.forEach(c => rushmoreSelections.push(c.childNodes[1].value))

        var rushmoreData = []
        rushmoreDataGrid.childNodes.forEach(c => {
            var tempArray = []
            var t = [c.childNodes[1], c.childNodes[2], c.childNodes[3], c.childNodes[4]]
            t.forEach(d => tempArray.push(d.childNodes[1].value))
            rushmoreData.push(tempArray)
        })


        var flushmoreSelections= []
        flushmoreSelection.childNodes.forEach(c => flushmoreSelections.push(c.childNodes[1].value))

        var flushmoreData = []
        flushmoreDataGrid.childNodes.forEach(c => {
            var tempArray = []
            var t = [c.childNodes[1], c.childNodes[2], c.childNodes[3], c.childNodes[4]]
            t.forEach(d => tempArray.push(d.childNodes[1].value))
            flushmoreData.push(tempArray)
        })


        var finalObj = {
            rmSelection: rushmoreSelections,
            rmData: rushmoreData,
            rmResult: rushmoreResult,
            fmSelection: flushmoreSelections,
            fmData: flushmoreData,
            fmResult: flushmoreResult,
        }

        var password = event.target.childNodes[2].childNodes[1].value

        this.sendPodcastPost(rushmoreSelections,rushmoreData,rushmoreResult,flushmoreSelections,flushmoreData,flushmoreResult,password,this.props.date)
    }

    sendPodcastPost = (rushSelect,rushData,rushResult,flushSelect,flushData,flushResult,password, date) => {
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ 
                date: date.toLocaleDateString(),
                mount_rushmore_select_order: JSON.stringify(rushSelect),
                mount_rushmore_select_data: JSON.stringify(rushData),
                mount_rushmore_ranking: JSON.stringify(rushResult),
                mount_flushmore_select_order: JSON.stringify(flushSelect),
                mount_flushmore_select_data: JSON.stringify(flushData),
                mount_flushmore_ranking: JSON.stringify(flushResult),
                password: password
             })
        };

        fetch(apiUrl.concat(savePodcastEndpoint), requestOptions)            
        .then(res => {
            this.props.onSavedChange(res.ok)
        },
        (error) => {
            console.log(error);
            this.props.onSavedChange(false)
        })
    }

    render() {
        let {
            people
        } = this.props;

        return <div className="podcastform">
            <form onSubmit={this.handleSubmit}>
                <Draft title="Mount Rushmore" imgPath="/projects/fourbifour/images/MountRushmore.png" people={people} 
                                                                                  data={this.props.rushmoreData} 
                                                                                  onOrderChange={this.props.onOrderChangeRushmore}
                                                                                  onTextChange={this.props.onTextChangeRushmore} 
                                                                                  onRankingChange={this.props.onRankingChange}
                                                                                  draftType={"Rush"} />
                <Draft title="Mount Flushmore" imgPath="/projects/fourbifour/images/MountFlushmore.png" people={people} 
                                                                                    data={this.props.flushmoreData} 
                                                                                    onOrderChange={this.props.onOrderChangeFlushmore}
                                                                                    onTextChange={this.props.onTextChangeFlushmore}
                                                                                    onRankingChange={this.props.onRankingChange}
                                                                                    draftType={"Flush"} />
                <div className="elementcontainer passwordDiv">
                    <label htmlFor="pass"><h3>Password</h3></label>
                    <input type="password" id="pass" name="pass"/>
                    <input type="submit" value="Save"></input>
                    {this.props.saved && <h4>Saved</h4>}
                    {this.props.saved === false && <h4>Did not save</h4>}
                </div>
            </form>
        </div>;
    }    
}