import React from 'react';
import './DraftRound.scss'
import { DraftRoundItem } from '../DraftRoundItem/DraftRoundItem';

export class DraftRound extends React.Component {
    render() {
        let {
            order,
            roundNumber,
            data
        } = this.props;

        return <div className="draftround" id={roundNumber}>
            <p>Round {roundNumber}</p>
            {
                order && order.map((value,index) => {
                    return <DraftRoundItem itemId={index} title={value} key={index} data={data ? data[index] : undefined} onTextChange={this.props.onTextChange} draftType={this.props.draftType}/>
                })
            }
        </div>;
    }    
}