import React from 'react';
import './DraftReview.scss'
import { DraftOrderSelectionOption } from '../DraftOrderSelection/DraftOrderSelectionOption/DraftOrderSelectionOption';

export class DraftReview extends React.Component {
    render() {
        let {
            options,
            data,
            name
        } = this.props;

        return <div className="draftreview">
            <h2>Winner</h2>
            {/* <DraftOrderSelectionOption options={options} data={data ? data : undefined}/> */}
            <div className="radio-box" onChange={this.props.onRankingChange}>
                { data == "Fred" && <input className="radio-btn" type="radio" name={name} id={"Fred"} checked/> }
                { data != "Fred" && <input className="radio-btn" type="radio" name={name} id={"Fred"}/> }
                <label className="radio-label">Fred</label>
                { data == "Mitch White" && <input className="radio-btn" type="radio" name={name} id={"Mitch White"} checked/> }
                { data != "Mitch White" && <input className="radio-btn" type="radio" name={name} id={"Mitch White"}/> }
                <label className="radio-label">Mitch White</label>
                { data == "PA" && <input className="radio-btn" type="radio" name={name} id={"PA"} checked/> }
                { data != "PA" && <input className="radio-btn" type="radio" name={name} id={"PA"}/> }
                <label className="radio-label">PA</label>
                { data == "Cam" && <input className="radio-btn" type="radio" name={name} id={"Cam"} checked/> }
                { data != "Cam" && <input className="radio-btn" type="radio" name={name} id={"Cam"}/> }
                <label className="radio-label">Cam</label>
                <span className="marker"></span>
            </div>
        </div>;
    }    
}