import React from 'react';
import './AppContainer.scss'
import { Calendar } from '../../components/Calendar/Calendar'
import { PodcastForm } from '../../components/PodcastForm/PodcastForm'
import { getAllPodcastsEndpoint, apiUrl } from '../../config'

export class AppContainer extends React.Component {

    state = {
        loaded: false,
        error: false,
        selectedDate: null,
        data: undefined
    }

    setDate = (date) => {
        this.setState({selectedDate: date});
        this.updateDataState(this.getPodcastInfo(date))
    }

    updateDataState(podcast) {
        if (podcast) {
            this.setState({data: {
                rushmoreData: {
                    ranking: podcast.mr_ranking,
                    data: podcast.mr_selectData,
                    order: podcast.mr_selectOrder
                },
                flushmoreData: {
                    ranking: podcast.mf_ranking,
                    data: podcast.mf_selectData,
                    order: podcast.mf_selectOrder
                }
            }})
        } else {
            this.setState({data: {
                rushmoreData: {
                    ranking: undefined,
                    data: [ ["","","",""],
                            ["","","",""],
                            ["","","",""],
                            ["","","",""] ],
                    order: [undefined,undefined,undefined,undefined]
                },
                flushmoreData: {
                    ranking: undefined,
                    data: [ ["","","",""],
                            ["","","",""],
                            ["","","",""],
                            ["","","",""] ],
                    order: [undefined,undefined,undefined,undefined]
                }
            }})
        }

        this.setState({saved:undefined})
    }

    componentDidMount() {
        fetch(apiUrl + getAllPodcastsEndpoint)
            .then(res => {
                var x = res.json();
                return x
            })
            .then(
            (result) => {
                var podcastData = result.map(p => {
                    return {
                        id: p.id,
                        date: new Date(Date.parse(p.date) + 79200000),
                        mr_selectOrder: JSON.parse(p.mr_selectOrder),
                        mr_selectData: JSON.parse(p.mr_selectData),
                        mr_ranking: JSON.parse(p.mr_ranking),
                        mf_selectOrder: JSON.parse(p.mf_selectOrder),
                        mf_selectData: JSON.parse(p.mf_selectData),
                        mf_ranking: JSON.parse(p.mf_ranking)
                    }
                })

                this.setState({podcasts:podcastData});
                this.setState({loaded:true});
            },
            // Note: it's important to handle errors here
            // instead of a catch() block so that we don't swallow
            // exceptions from actual bugs in components.
            (error) => {
                console.log(error);
                this.setState({error:true});
            }
        )
    }

    getPodcastInfo = (date) => {
        if (this.state.podcasts) {
            return this.state.podcasts.find((p) => new Date(Date.parse(p.date)).toDateString() === date.toDateString());
        }
    }

    setRushmoreOrder = (index,value) => {
        if (value === "-") {
            value = undefined;
        }
        let newOrder = [];

        for (var i = 0; i < this.state.data.rushmoreData.order?.length;i++) {
            if (i === index) {
                newOrder.push(value);
            } else {
                newOrder.push(this.state.data.rushmoreData.order[i]);
            }
        }

        this.setState(prev => ({
            ...prev,
            data: {
                ...prev.data,
                rushmoreData: {
                    ...prev.data.rushmoreData,
                    order: newOrder
                }
            }
        }));
    }

    setFlushmoreOrder = (index,value) => {
        if (value === "-") {
            value = undefined;
        }
        let newOrder = [];

        for (var i = 0; i < this.state.data.flushmoreData.order?.length;i++) {
            if (i === index) {
                newOrder.push(value);
            } else {
                newOrder.push(this.state.data.flushmoreData.order[i]);
            }
        }

        this.setState(prev => ({
            ...prev,
            data: {
                ...prev.data,
                flushmoreData: {
                    ...prev.data.flushmoreData,
                    order: newOrder
                }
            }
        }));
    }

    setRushmoreTextData = (e) => {

        var newData = []

        for (var i = 0; i < this.state.data.rushmoreData.data.length; i++) {
            var newSubData = []
            if (i + 1 == e.target.parentElement.parentElement.id) {
                for (var j = 0; j < this.state.data.rushmoreData.data[i].length; j++) {
                    if (j == e.target.id) {
                        newSubData.push(e.target.value)
                    } else {
                        newSubData.push(this.state.data.rushmoreData.data[i][j])
                    }
                }
            } else {
                newSubData = [...this.state.data.rushmoreData.data[i]]
            }

            newData.push(newSubData)
        }

        this.setState(prev => ({
            ...prev,
            data: {
                ...prev.data,
                rushmoreData: {
                    ...prev.data.rushmoreData,
                    data: newData
                }
            }
        }));
    }

    setFlushmoreTextData = (e) => {
        var newData = []

        for (var i = 0; i < this.state.data.flushmoreData.data.length; i++) {
            var newSubData = []
            if (i + 1 == e.target.parentElement.parentElement.id) {
                for (var j = 0; j < this.state.data.flushmoreData.data[i].length; j++) {
                    if (j == e.target.id) {
                        newSubData.push(e.target.value)
                    } else {
                        newSubData.push(this.state.data.flushmoreData.data[i][j])
                    }
                }
            } else {
                newSubData = [...this.state.data.flushmoreData.data[i]]
            }

            newData.push(newSubData)
        }

        this.setState(prev => ({
            ...prev,
            data: {
                ...prev.data,
                flushmoreData: {
                    ...prev.data.flushmoreData,
                    data: newData
                }
            }
        }));
    }

    setWinner = (e) => {
        var winner = e.target.id

        if (e.target.name == "Mount Rushmore") {
            this.setState(prev => ({
                ...prev,
                data: {
                    ...prev.data,
                    rushmoreData: {
                        ...prev.data.rushmoreData,
                        ranking: winner
                    }
                }
            }));
        }

        if (e.target.name == "Mount Flushmore") {
            this.setState(prev => ({
                ...prev,
                data: {
                    ...prev.data,
                    flushmoreData: {
                        ...prev.data.flushmoreData,
                        ranking: winner
                    }
                }
            }));
        }
    }

    setSaved = (saved) => {
        this.setState({saved:saved})
    }

    render() {
        return <div className="appcontainer">
            <div className="elementcontainer"> 
                <h1>Four Bi Four Podcast</h1>
                <img src="/projects/fourbifour/images/4by4car.jpg"  height="300"/>
                <img src="/projects/fourbifour/images/4by4wood.jpg"  height="300"/>
                { this.state.loaded && <Calendar podcasts={this.state.podcasts} dateChange={this.setDate}/> }
            </div>
            { this.state.selectedDate && <PodcastForm people={['Fred','PA','Cam','Mitch White']} 
                                                      rushmoreData={this.state.data.rushmoreData}
                                                      onOrderChangeRushmore={this.setRushmoreOrder}
                                                      onTextChangeRushmore={this.setRushmoreTextData} 
                                                      flushmoreData={this.state.data.flushmoreData}
                                                      onOrderChangeFlushmore={this.setFlushmoreOrder}
                                                      onTextChangeFlushmore={this.setFlushmoreTextData} 
                                                      date={this.state.selectedDate}
                                                      onRankingChange={this.setWinner}
                                                      onSavedChange={this.setSaved}
                                                      saved={this.state.saved}/>}
            { this.state.error && <h3>Error. Damn, damn, damn...</h3> }
        </div>;
    }    
}